@import "../../base/fn";

.weui-btn_loading {
    .weui-loading {
        margin: -0.2em 0.34em 0 0;
    }
    &.weui-btn_primary {
        background-color: @weuiBtnPrimaryActiveBg;
        color: var(--weui-WHITE);
    }
    &.weui-btn_default {
        background-color: @weuiBtnDefaultActiveBg;
    }
    &.weui-btn_warn {
        background-color: @weuiBtnWarnActiveBg;
    }
}
